<template>
    <div class="page_main">
        <el-row class="rowStyle">
            <el-form ref="formBoard" class="formBoard" :model="formBoard"  label-width="80px">
                <el-form-item label="疾病名称" class="left marginR">
                    <el-input v-model="formBoard.name"  placeholder="请输入疾病名称"></el-input>
                </el-form-item>
                <el-form-item label="疾病分类"  class="left marginR">
                    <!-- <el-input v-model="formBoard.card" placeholder="请输入模板分类"></el-input> -->
                    <el-select v-model="formBoard.check" clearable  placeholder="请选择疾病分类">
                            <!-- <el-option label="检查类" value="检查类"></el-option> -->
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item  class="left">
                    <el-button type="primary" plain>查询</el-button>
                </el-form-item>
            </el-form>
            <el-table :data="tableData"  :header-cell-style="{background:'#FAFAFA',height:'48px'}" size='' style="width: 100%">
                <el-table-column prop="userName" label="疾病名称" width=""></el-table-column>  
                <el-table-column prop="personNo" label="疾病分类" width=""></el-table-column>
                <el-table-column prop="scaleName" label="是否高危" width="">
                        <!-- <template slot-scope="subTime">
                                    {{subTime.row.stime.substring(0,10)}}
                                </template> -->
                </el-table-column>
                <el-table-column prop="userName" label="标识" width=""></el-table-column>  
                <el-table-column prop="userName" label="关联专家" width=""></el-table-column>  
            </el-table>
            <div class="page_bottom">
                <!-- <div class="block">
                    <el-pagination
                    background
                    :hide-on-single-page="value"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div> -->
                <div class="left bottom_btn">
                    <el-button type="primary" plain  size="small" class="right" @click="addBoard()">新  增</el-button>
                </div>
            </div>
        </el-row>
        <el-dialog
            title="新增疾病"
            :visible.sync="dialogVisible"
            width="50%"
            >
            <el-form ref="form" class="form" :model="form" label-width="80px">
                <el-row :gutter="20">
                    <el-col :span="10" :offset="2">
                        <el-form-item label="疾病名称">
                            <el-input v-model="form.name"  placeholder="请输入疾病名称"></el-input>
                        </el-form-item>
                        <el-form-item label="是否高危">
                            <!-- <el-input v-model="form.height"  placeholder="请输入身高">
                                <template slot="append">cm</template>
                            </el-input> -->
                            <el-select v-model="form.highRisk" clearable  placeholder="请选择是否高危">
                                <el-option label="是" value="1"></el-option>
                                <el-option label="否" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                
                    <el-col :span="10">
                        <el-form-item label="疾病分类"  class="">
                            <el-select v-model="form.classify" clearable  placeholder="请选择疾病分类">
                                    <el-option
                                        v-for="item in optionsAdd"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="关联专案">
                            <!-- <el-input v-model="form.workUnits"  placeholder="请输入工作单位"></el-input> -->
                            <el-select v-model="form.associat" clearable  placeholder="请选择关联专案">
                                    <el-option
                                        v-for="item in associatList"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>

export default {
    data(){
       return {
           formBoard: {
                name: '',
                check: ''
             }, 
            tableData: [
               {
                    userName: '1111',
                    personNo: '1111',
                    scaleName: '1111',
                    userName: '1111',
               }
           ],
           currentPage4: 1,
            total: 0,
            pageNumber: 1,
            pageSize: 10,
            value: false,
            options: [
                {
                    value: '选项1',
                    label: '体弱儿疾病'
                },
                {
                    value: '选项2',
                    label: '胎儿期高危因素'
                },
                
            ],
            dialogVisible: false,
            form: {
               name: '',
               classify: '',
               highRisk: '',
               associat: ''
           },
           optionsAdd: [
                {
                    value: '选项1',
                    label: '体弱儿疾病'
                },
                {
                    value: '选项2',
                    label: '胎儿期高危因素'
                },
                
            ],
            associatList: [
                {
                    value: '选项1',
                    label: '体弱儿疾病'
                },
                {
                    value: '选项2',
                    label: '胎儿期高危因素'
                },
            ]
       }
      
    },
    components:{
      
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
           
           
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            
        },
        addBoard(){
            this.dialogVisible = true
        }
    },
}
</script>
<style lang="less" scoped>
.page_main{
    width: 100%;
    height: 550px;
    .left{
        float: left;
    }
    .formBoard{
        height: 60px;
        margin-top: 20px;
    }
    
    .page_bottom{
        height: 50px;
        width: 100%;
        position: absolute;
        bottom: 0;
        .block{
            float: left;
            margin-top: 20px;
        }
        .bottom_btn{
            float: right;
            margin-right: 20px;
        }
        
    }
    .rowStyle{
        height:100%;
        border-top:8px solid #F0F2F5;
        padding:4px;
    }
}
</style>
