<template>
    <div class="page_main">
        <el-row style="height:100%;">
            <el-col :span="12" class="colLeft">
                <el-form ref="formBoard" class="formBoard" :model="formBoard"  label-width="80px">
            <el-form-item label="专案名称"  class="left marginR">
                    <el-select v-model="formBoard.check" clearable  placeholder="请选择专案名称">
                            <el-option label="孕期疾病" value="1"></el-option>
                    </el-select>    
                </el-form-item>
                <el-form-item  class="left">
                    <el-button type="primary" plain>查询</el-button>
                </el-form-item>
            </el-form>
                <el-table :data="tableData" border :header-cell-style="{background:'#FAFAFA',height:'48px'}" size='' style="width: 100%">
                    <el-table-column prop="userName" label="专案名称" width=""></el-table-column>  
                    <el-table-column prop="personNo" label="疾病名称" width=""></el-table-column>
                    <el-table-column
                        label="操作"
                        width="100">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="edit()">编辑</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page_bottom">
                    <div class="block">
                        <el-pagination
                        background
                        :hide-on-single-page="value"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage4"
                        :page-size="10"
                        layout="total, prev, pager, next, jumper"
                        :total="total">
                        </el-pagination>
                    </div>
                    <div class="left bottom_btn">
                        <el-button type="primary" plain  size="small" class="right" @click="addBoard()">新增</el-button>
                    </div>
                </div>
            </el-col>
            <el-col :span="12" class="colRight">
                <el-form v-if="flag" ref="form" class="form" :model="form" label-width="80px">
                <el-row :gutter="20">
                    <el-col :span="14" :offset="4">
                         <el-form-item v-if="editFlag"   label="类型" style="margin-top:20px;">
                            <!-- <el-input v-model="form.name"  placeholder="请输入专案名称"></el-input> -->
                            <el-select v-model="form.name" disabled clearable  placeholder="请选择专案名称" style="width: 264px;">
                                    <el-option
                                        v-for="item in optionsAdd"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">    
                                    </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item  v-if="addFlag" label="专案名称" style="margin-top:20px;">
                            <!-- <el-input v-model="form.name"  placeholder="请输入专案名称"></el-input> -->
                            <el-select v-model="form.name" clearable  placeholder="请选择专案名称" style="width: 264px;">
                                    <el-option
                                        v-for="item in optionsAdd"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">    
                                    </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="对应专案"  class="">
                            <el-select v-model="form.classify" clearable  placeholder="请选择对应专案" style="width: 264px;">
                                    <el-option
                                        v-for="item in optionsAdd"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">    
                                    </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item  class="">
                            <el-button type="primary" plain @click="cancel()">取消</el-button>
                            <el-button  v-if="editFlag" type="primary" plain>保存编辑</el-button>
                            <el-button   v-if="addFlag" type="primary" plain>保存</el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            </el-col>
        </el-row>
    </div>
</template>
<script>

export default {
    data(){
       return {
           formBoard: {
                name: '',
                check: ''
             }, 
            tableData: [
               {
                    userName: '1111',
                    personNo: '1111',
                    scaleName: '1111',
                    userName: '1111',
               }
           ],
           currentPage4: 1,
            total: 0,
            pageNumber: 1,
            pageSize: 10,
            value: false,
            form: {
                 name: '',
                classify: ''
            },
             optionsAdd: [
                {
                    value: '选项1',
                    label: '体弱儿疾病'
                },
                {
                    value: '选项2',
                    label: '胎儿期高危因素'
                },
                
            ],
            flag: false,
            editFlag: false,
            addFlag: true

       }
      
    },
    components:{
      
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
           
           
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            
        },
        addBoard(){
            console.log(11111111)
            this.flag = true
            this.editFlag = false
            this.addFlag = true
        },
        cancel(){
            this.flag = false
            
        },
        edit(){
            this.flag = true
            this.editFlag = true
            this.addFlag = false
        }
    },
}
</script>
<style lang="less" scoped>
.page_main{
    width: 100%;
    height: 600px;
    .left{
        float: left;
    }
    .formBoard{
        height: 60px;
        margin-top: 20px;
    }
    
    .page_bottom{
        height: 70px;
        width: 50%;
        position: absolute;
        bottom: 0;
        .block{
            float: left;
            margin-top: 20px;
        }
        .bottom_btn{
            margin: 20px;
        }
        
    }
    .colLeft{
        height:100%;
        border-top:8px solid #F0F2F5;
        border-right:3px solid #F0F2F5 ;
        padding:4px;
    }
    .colRight{
        height:100%;
        border-top:8px solid #F0F2F5;
        border-left:3px solid #F0F2F5;
        padding:4px;
    }
}
</style>
